<template>
  <div>
    <V-Scroll ref="spellGroupScroll" :isList="false" :scroll-height="`${mainHeight}px`">
      <div class="banner">
        <swiper class="slide-box" :options="swiperOptions">
          <swiper-slide v-for="(item, index) in banners" :key="index">
            <img :src="item.src" width="100%" :alt="item.title">
          </swiper-slide>
          <!--<div style="position: absolute;right: 0.3rem;bottom: 0.29rem;z-index:9000;" class="swiper-pagination" slot="pagination"></div>-->
        </swiper>
        <!-- <div class="rule">查看活动规则</div> -->
      </div>
      <div class="remind" v-if="false">
        <div class="txt">
          <span class="icon-remind"></span>
          <span>每日上新提醒</span>
        </div>
        <div class="switch active">
          <div class="yuanquan"></div>
        </div>
      </div>
      <div class="chat" v-if="false">
        <p class="tit">官方福利群<span class="entry">进群</span></p>
        <p>邀你进群，领取免费好物、现金红包</p>
        <p>点金【进群】获取二维码</p>
      </div>
      <div class="list">
        <ul class="grouplist">
          <router-link tag="li" :to="{name: 'SpellGroupDetails', params: {id: item.groupBuyId}}" v-for="(item, index) in groupBuyList" :key="index">
            <img class="pro-img" :src="item.goodsUrl" :alt="item.goodsName">
            <div class="info">
              <div class="name">
                <p>{{item.goodsName | filterSubStrName(35)}}</p>
                <p class="save-money">
                  <span>立省{{item.save}}元</span>
                </p>
              </div>
              <div class="statu">
                <div class="left">
                  <span class="icon-sweet"></span>
                  <span class="sweet-num">{{item.integral}}</span>
                  <span class="people-num">{{item.groupPeopleNuber}}人团</span>
                </div>
                <div class="right">
                  <span>已送{{item.saledCount}}件</span>
                </div>
              </div>
            </div>
          </router-link>
        </ul>
      </div>
    </V-Scroll>
  </div>
</template>

<script>
import Ajax from "@/mixins/ajax";

// 拼团列表Url
const getGroupBuyListUrl = "/groupbuy/list/booking";

const swiperOptions = {
  pagination: {
    el: ".swiper-pagination",
    type: "fraction",
    renderFraction: (currentClass, totalClass) => `<span class="${currentClass}"></span>/<span class="${totalClass}"></span>`
  },
  autoplay: true
};

export default {
  name: "spellGroup",
  mixins: [Ajax],
  data() {
    return {
      swiperOptions,
      banners: [],
      groupBuyList: []
    };
  },
  mounted() {
    // 获取轮播图
    this.getSlideList(2).then(body => {
      const { banners } = body;
      this.banners = banners;
    });
    this.getGroupBuyList();
  },
  methods: {
    getGroupBuyList() {
      this.get(getGroupBuyListUrl).then(body => {
        this.groupBuyList = body.groupBuyGoodsVos;
        this.$nextTick(() => {
          this.$refs.spellGroupScroll.refresh();
        });
      });
    }
  }
};
</script>

<style scoped>
.slide {
  background: #fff;
  position: relative;
}
.slide,
.slide-box {
  height: 4.56rem;
  overflow: hidden;
}
.swiper-pagination {
  position: absolute;
  right: 0.3rem;
  bottom: 0.29rem;
  min-width: 1.08rem;
  max-width: 2rem;
  height: 0.73rem;
  background-color: rgba(53, 53, 53, 0.2);
  border-radius: 0.36rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 0.36rem;
}
.banner {
  position: relative;
  height: 4.56rem;
  z-index: 9999;
}
.rule {
  position: absolute;
  top: 0.3rem;
  left: 0;
  padding: 0.1rem 0.3rem;
  background: #df6c2a;
  color: #fff;
  border-radius: 0.5rem;
  transform: translateX(-0.3rem);
}
.remind {
  padding: 0 0.5rem;
  background: #fff;
  height: 1.45rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.remind .txt {
  display: flex;
  align-items: center;
}
.remind .switch {
  width: 1.5rem;
  height: 0.93rem;
  border-radius: 0.47rem;
  display: flex;
  align-items: center;
  background: #eee;
  justify-content: flex-start;
}
.remind .switch.active {
  background: #4bd964;
  justify-content: flex-end;
}
.remind .switch .yuanquan {
  width: 0.84rem;
  height: 0.84rem;
  border-radius: 50%;
  background: #fff;
}
.icon-remind {
  display: inline-block;
  width: 0.53rem;
  height: 0.65rem;
  background: url("../../images/icon-remind.png") no-repeat center/contain;
}
.chat {
  text-align: center;
  background: #fff7f4;
  padding: 0.45rem 0;
  margin: 0.3rem 0.43rem;
  border-top: solid 0.01rem #eace9e;
  border-bottom: solid 0.01rem #eace9e;
}
.chat p {
  font-size: 0.42rem;
  line-height: 0.54rem;
  color: #999;
}
.chat p.tit {
  font-size: 0.51rem;
  line-height: 0.51rem;
  color: #e74e11;
  padding-bottom: 0.4rem;
}
.chat p.tit .entry {
  font-size: 0.36rem;
  line-height: 0.6rem;
  width: 1.2rem;
  height: 0.6rem;
  display: inline-block;
  background: url("../../images/icon-entry.png") no-repeat center/contain;
  color: #fff;
  margin-left: 0.2rem;
  position: relative;
  bottom: 0.1rem;
}
.grouplist {
  padding-top: 0.3rem;
}
.grouplist li {
  padding: 0.45rem;
  background: #fff;
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.3rem;
}
.grouplist img.pro-img {
  width: 3rem;
  height: 3rem;
  background: #fff;
}
.grouplist .info {
  flex: 1;
  padding-left: 0.45rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.grouplist .info p.save-money {
  padding-top: 0.3rem;
}
.grouplist .info p.save-money span {
  padding: 0.19rem 0.23rem;
  border: 0.02rem solid #de2245;
  color: #de2245;
  font-size: 0.36rem;
  border-radius: 0.36rem;
}
.grouplist .info .statu {
  display: flex;
  justify-content: space-between;
}
.grouplist .info .left {
  display: flex;
  align-items: flex-end;
}
.grouplist .info .right {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.sweet-num {
  font-size: 0.81rem;
  line-height: 0.81rem;
  color: #de2245;
}
.icon-sweet {
  display: inline-block;
  position: relative;
  bottom: 0.1rem;
  /* vertical-align: middle; */
  width: 0.6rem;
  height: 0.54rem;
  background: url("../../images/icon-currency.png") no-repeat center/contain;
}
</style>



